.financeCharge-wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  .financeCharge-main {
    --fcinput-width: 200px;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .fc-header {
      width: 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: flex-end;
      .fc-title {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-items: flex-end;
        width: 50%;
        .fc-cp-input {
          margin-bottom: -20px;
          margin-left: 10px;
          width: fit-content;
          font-weight: 600;
          .fc-c--checkbox-title {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: flex-start;
            align-items: baseline;
            align-content: center;
          }
          .cycleCodeFormBox {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
            align-items: flex-end;
            align-content: center;
            width: 280px;
            .cycleCodeInputSpace {
              width: 100px;
            }
            .setCycleCodeButton {
              width: 150px;
              margin-top: -65px;
              height: 50px;
            }
          }
        }
      }
      .fc-buttons {
        margin-top: 5px;
        margin-bottom: 10px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-end;
        > * {
          margin-top: 5px;
          margin-right: 5px;
        }
      }
    }
    .fc-controls {
      display: flex;
      flex-direction: column;
      align-items: center;
      form {
        width: 100%;
      }
      .fc-control-inputs {
        margin-bottom: 10px;
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        input {
          min-height: 51px;
          min-width: var(--fcinput-width);
        }
      }
      .fc-table {
        margin-top: 10px;
        width: 100%;
      }
    }
    .cp-controls {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      form {
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
      }
      .cp-control-inputs {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        div {
          margin-right: 12px;
        }
        input {
          min-height: 51px;
          min-width: var(--fcinput-width);
        }
      }
      .cp-table {
        width: 100%;
      }
    }
  }
}
