.creditProtTableOddRow {
  background-color: rgb(235, 235, 235);
}
.editHover:hover {
  color: var(--blue);
  cursor: pointer;
}
.labelAndInputTop {
  height: 25%;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  align-content: space-around;
}
.labelAndInputBottom {
  height: 75%;
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: stretch;
  align-content: stretch;
}

.labelAndInput {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: flex-start;
  align-content: stretch;
  width: 75%;
}
.creditProtTableInput {
  height: 35px;
}
.cpRemoveCellDiv .removeTooltipText {
  visibility: hidden;
  background-color: #ffffff;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  margin-top: 20px;
  margin-left: -100px;
  color: black;
  box-shadow: 2px 3px 5px grey;
}

.cpRemoveCellDiv:hover {
  cursor: pointer;
  color: var(--blue);
}
