.zdate-wrapper {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  width: 100%;
  .zdate-main {
    width: 80%;
    .header-div {
      margin-top: 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
  .zdate-container {
    .zdate-input-container {
      padding: 5px;

      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: flex-start;
      align-items: center;
      align-content: stretch;
      > * {
        padding: 5px;
      }
    }

    .date-input {
      padding-right: 10px;
    }

    .code-header {
      padding-right: 15px;
    }

    .cyclecode-input > label > input {
      width: 100%;
    }

    .bad-days {
      font-weight: bold;
      color: white;
      background-color: rgb(235, 0, 0);
    }
    .good-days {
      font-weight: bold;
      color: white;
      background-color: green;
    }
  }
}

.zDateHandleAndCopy {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  align-content: flex-start;
}
