.datecalc-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  min-height: 100vh;

  .DateCalcTitle {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .datecalc-main {
    width: 80%;
  }

  .DateCalcTitle {
    width: 100%;
    display: flex;
    align-items: flex-start;
  }
  .DateCalc {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
  }
}
.dateCalcCopyExport {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  align-content: flex-start;
}
