.financeChargeTable {
  width: 100%;
  .editHover:hover {
    cursor: pointer;
  }
  .remove-span:hover {
    color: var(--blue);
    cursor: pointer;
  }
  .labelAndInputTop {
    height: 25%;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
    align-content: space-around;
  }
  .labelAndInputBottom {
    height: 75%;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: stretch;
    align-content: stretch;
  }

  .labelAndInput {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: flex-start;
    align-content: stretch;
    width: 75%;
  }
  .financeChargeTableInput {
    height: 35px;
  }
}
.financeChargeTableOddRow {
  background-color: rgb(235, 235, 235);
}
.financeChargeTableRemoveCell:hover {
  color: var(--blue);
  cursor: pointer;
}
.editHover:hover {
  color: var(--blue);
  cursor: pointer;
}
